import { Link } from "react-router-dom";
import styled from "styled-components";
import { mainStyle } from "../../styles/GlobalStyle";

const Section = styled.div`
  padding: ${mainStyle.padding};
  margin-bottom: 120px;
`;
const ConWrap = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
const Con = styled.div`
  display: flex;
  flex-direction: column;
  a {
    color: ${mainStyle.mainColor};
    letter-spacing: 0;
  }
  div {
    width: 95%;
    height: 75vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  img {
    width: 110%;
    display: block;
    transition: 0.5s;
  }
  video {
    width: 130%;
  }
  h3 {
    font-size: 22px;
    font-weight: 600;
    margin: 15px;
  }
  p {
    width: 50%;
    font-size: 14px;
    font-weight: 400;
    margin: 0 15px;
  }
  &:hover img {
    transform: scale(0.95);
  }
  &:hover h3 {
    text-decoration: underline;
  }
`;

export const Section3 = () => {
  return (
    <Section>
      <ConWrap>
        <Con>
          <Link to="">
            <div>
              <img src="https://basic-admin.basicagency.com/uploads/Work/Patagonia/Images/_720xAUTO_crop_center-center_65_none/Patagonia-Case-Study-Thumbnail.jpg"></img>
            </div>
            <h3>PATAGONIA</h3>
            <p>AN ECOMMERCE EXPERIENCE DRIVEN BY PATAGONIAS BRAND MISSION</p>
          </Link>
        </Con>
        <Con>
          <Link to="">
            <div>
              <img src="https://basic-admin.basicagency.com/uploads/Work/Wilson/Images/_720xAUTO_crop_center-center_65_none/WIlson-Case-Study-Thumbnail.jpg"></img>
            </div>
            <h3>WILSON</h3>
            <p>A CENTURY-OLD SPORTS BRAND FINDING ITS PLACE IN CULTURE</p>
          </Link>
        </Con>
        <Con>
          <Link to="">
            <div>
              <video loop autoPlay muted>
                <source
                  src="https://basic-admin.basicagency.com/uploads/Work/Google-Store/Video/Google-Store-Web-Design-Case-Study-Thumbnail-02.mp4"
                  type="video/mp4"
                />
              </video>
            </div>
            <h3>GOOGLE STORE</h3>
            <p>
              AN ECOMMERCE EXPERIENCE HELPING GOOGLE BRING ITS HARDWARE TO
              PEOPLE ACROSS THE GLOBE
            </p>
          </Link>
        </Con>
      </ConWrap>
    </Section>
  );
};
