import styled from "styled-components";
import { mainStyle } from "../../styles/GlobalStyle";

const Wrap = styled.div`
  display: flex;
  justify-content: space-between;
  padding: ${mainStyle.padding};
  margin-top: 120px;
`;
const TextWrap = styled.div`
  width: 50%;
  color: ${mainStyle.mainColor};
  h1 {
    font-size: 110px;
    font-weight: 900;
  }
  h5 {
    font-size: 20px;
    font-weight: 600;
    margin-top: 30px;
    span {
      font-weight: 800;
    }
  }
`;
const Btn = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 30px;
  border: 1px solid ${mainStyle.mainColor};
  border-radius: 25px;
  font-size: 12px;
  font-weight: 600;
  margin-top: 100px;
  transition: 0.5s;
  &:hover {
    background-color: ${mainStyle.mainColor};
    color: white;
  }
`;
const Video = styled.div`
  width: 50%;
  overflow: hidden;
  video {
    height: 100%;
  }
`;

export const Section5 = () => {
  return (
    <Wrap>
      <TextWrap>
        <h1>
          BASIC/DEPT
          <br />
          HELPS
          <br />
          BRANDS
          <br /> CONNECT
          <br />
          W/ CULTURE
        </h1>
        <h5>
          ADWEEK <span>(AGENCY SPOTLIGHT)</span>
        </h5>
        <Btn>ABOUT US</Btn>
      </TextWrap>
      <Video>
        <video loop autoPlay muted>
          <source
            src="https://basic-admin.basicagency.com/uploads/Culture-Loop_v1.mp4"
            type="video/mp4"
          />
        </video>
      </Video>
    </Wrap>
  );
};
